import { useEffect, useState } from "react"
import { csv2json } from "json-2-csv"
import Chart from "./Chart"
import "./App.css"

function App() {
  const [data, setdata] = useState()
  const [cities, setCities] = useState()
  useEffect(() => {
    const fetchData = async () => {
      const water = await fetch(
        "https://www.data.gouv.fr/fr/datasets/r/2963ccb5-344d-4978-bdd3-08aaf9efe514"
      ).then((res) => res.text())
      setdata(
        csv2json(water, {
          delimiter: { field: ";" },
        })
      )
    }
    const fetchCities = async () => {
      const data = await fetch(
        "https://www.data.gouv.fr/fr/datasets/r/dd9cf705-a759-46c6-afd6-bc85cf25f363"
      ).then((res) => res.text())
      setCities(
        csv2json(data, {
          delimiter: { field: ";" },
        })
      )
    }
    fetchData()
    fetchCities()
  }, [])

  return (
    <div className="App">
      <header>
        <h1>Surveillance du Sars-CoV-2 dans les eaux uses</h1>
        <p>
          Un graphique en temps rel de la surveillance de la concentration de
          Sars-CoV-2 dans les eaux uses. C'est un indicateur de l'intensit des
          vagues de circulation virale pour le suivi de pandmie de Covid-19 en
          France.
        </p>
      </header>
      <Chart data={data} cities={cities} />
      <h2>A propos</h2>
      <p>
        {" "}
        Le dispositif franais de surveillance des eaux uses{" "}
        <abbr title="Dispositif franais de surveillance des eaux uses">
          SUMEau
        </abbr>{" "}
        a t initi sous limpulsion de la direction gnrale de la sant et de
        la direction de leau et de la biodiversit avec lappui technique et
        scientifique des agences sanitaires. La surveillance des eaux uses
        poursuit deux objectifs, qui sont de dtecter prcocement la prsence du
        gnome du SARS-CoV-2 en population gnrale et de suivre les tendances
        de la circulation virale au sein de la population.
      </p>
      <p>
         Ces donnes sont actualises en <strong>temps rel</strong>  partir
        de{" "}
        <a href="https://www.data.gouv.fr/fr/datasets/surveillance-du-sars-cov-2-dans-les-eaux-usees-sumeau/">
          Datagouv
        </a>
        . Les donnes de Sum'Eau sont livres  Datagouv avec une  deux
        semaines de dcalage, donc gardez  l'esprit que c'est un indicateur de
        la circulation virale de la semaine dernire.
      </p>
    </div>
  )
}

export default App
