import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ReferenceLine,
  Label,
  Brush,
} from "recharts"

import { useState } from "react"

import moment from "moment"

export default function Chart({ data = [], cities = [] }) {
  const colors = [
    "#f44336",
    "#e81e63",
    "#9c27b0",
    "#673ab7",
    "#3f51b5",
    "#2196f3",
    "#03a9f4",
    "#00bcd4",
    "#009688",
    "#4caf50",
    "#8bc34a",
    "#cddc39",
    "#ffeb3b",
    "#ffc107",
    "#ff9800",
    "#ff5722",
    "#795548",
    "#9e9e9e",
    "#607d8b",
  ]

  const xAxis = "semaine"

  const waterValues = data
    ?.map((week) =>
      Object.entries(week)
        // remove undefined values for a city this week
        .filter(([name, value]) => value !== "NA")
        // format the city name according to cities dataset
        .map(([name, value]) => {
          if (name !== "National" && name !== xAxis) {
            name = cities
              ?.find(({ nom }) => name === nom)
              ?.["commune"].toLowerCase()
          }
          return [name, value]
        })
        // re-transform as object
        .reduce(
          (accumulator, [name, value]) => ({
            ...accumulator,
            [name]:
              name === xAxis
                ? //
                  moment(value, "YYYY-W").add(7, "d").format("YYYY-MM-DD")
                : Number(value.split(",")[0]),
          }),
          {}
        )
    )
    // set national in top position of the object

    .map(
      (week) =>
        (week = Object.assign({ rennes: week.rennes }, week)) &&
        (week = Object.assign({ nantes: week.nantes }, week)) &&
        (week = Object.assign({ lille: week.lille }, week)) &&
        (week = Object.assign({ toulouse: week.toulouse }, week)) &&
        (week = Object.assign({ marseille: week.marseille }, week)) &&
        (week = Object.assign({ paris: week.paris }, week)) &&
        (week = Object.assign({ National: week.National }, week))
    )

  const citiesName = [
    ...new Set(
      waterValues
        ?.map((data) => Object.keys(data || []).filter((key) => key !== xAxis))
        .flat()
    ),
  ]

  const [showCity, setShowCity] = useState([
    "National",
    "paris",
    "marseille",
    "toulouse",
  ])

  const filterWaterValue = (weeks, cityNames) =>
    weeks.map((week) =>
      Object.entries(week)
        .filter(([name, value]) => cityNames.includes(name) || name === xAxis)
        .reduce(
          (accumulator, [name, value]) => ({ ...accumulator, [name]: value }),
          {}
        )
    )

  const current = filterWaterValue(waterValues, showCity)

  return (
    <>
      <ResponsiveContainer
        width="90%"
        height={400} // will se max-height
        style={{ overflow: "visible", display: "block" }}
      >
        <LineChart
          data={current}
          margin={{
            right: 30,
            left: 20,
          }}
        >
          {citiesName.map((line, index) => (
            <Line
              strokeWidth={line === "National" ? 5 : 2}
              connectNulls
              type="monotone"
              dot={false}
              dataKey={line}
              key={line}
              stroke={showCity.includes(line) ? colors[index] : "#cccccc"}
              name={line}
              id={line}
            ></Line>
          ))}
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis tickMargin={5} dataKey={xAxis} />
          <YAxis
            label={{
              value: "Concentration virale cg/L",
              angle: -90,
              position: "left",
              textAnchor: "middle",
            }}
          ></YAxis>
          <Brush
            travellerWidth={15}
            dataKey={(a) => {
              return a[xAxis]
            }}
          />
          <Legend
            width={100}
            align="center"
            wrapperStyle={{
              width: "100%",
            }}
            onClick={(e) => {
              if (showCity.includes(e.value)) {
                setShowCity(showCity.filter((name) => name !== e.value))
              } else {
                setShowCity([...showCity, e.value])
              }
            }}
          />
          <Tooltip
            itemSorter={(e) => {
              return -e.value
            }}
            contentStyle={{
              background: "rgb(33, 37, 43)",
              textAlign: "left",
            }}
          />
          {document?.location?.hash === "#personne" && (
            <ReferenceLine
              x="2022-08-29"
              stroke="transparent"
              label={
                <Label
                  offset={40}
                  value="C'est un rhume"
                  position="insideBottom"
                />
              }
            />
          )}
          {document?.location?.hash === "#personne" && (
            <ReferenceLine
              x="2022-12-12"
              stroke="transparent"
              label={
                <Label
                  offset={15}
                  value="Personne n'aurait pu prdire que"
                  position="insideTop"
                />
              }
            />
          )}
          {document?.location?.hash === "#personne" && (
            <ReferenceLine
              x="2023-01-30"
              stroke="transparent"
              label={
                <Label
                  offset={40}
                  value="C'est saisonnier"
                  position="insideBottom"
                />
              }
            />
          )}
          {document?.location?.hash === "#personne" && (
            <ReferenceLine
              x="2023-07-10"
              stroke="transparent"
              label={
                <Label
                  offset={40}
                  value="C'est termin"
                  position="insideBottom"
                />
              }
            />
          )}
          {document?.location?.hash === "#personne" && (
            <ReferenceLine
              x="2023-12-18"
              stroke="transparent"
              label={
                <Label
                  offset={15}
                  value="Personne n'aurait pu prvoir a"
                  position="insideTop"
                />
              }
            />
          )}
          {document?.location?.hash === "#personne" && (
            <ReferenceLine
              x="2024-04-08"
              stroke="transparent"
              label={
                <Label
                  offset={40}
                  value="Immunit de masse"
                  position="insideBottom"
                />
              }
            />
          )}
          {document?.location?.hash === "#personne" && (
            <ReferenceLine
              x={current.pop()?.[xAxis]}
              stroke="transparent"
              label={
                <Label
                  offset={40}
                  value="C'est termin ?"
                  position="insideBottom"
                />
              }
            />
          )}
        </LineChart>
      </ResponsiveContainer>
      <p className="info">
         Cliquez pour slectioner le nom des villes dans la lgende, ou
        afficher{" "}
        <button
          className="reset"
          data={{}}
          onClick={(e) => {
            showCity.length === citiesName.length
              ? setShowCity(["National"])
              : setShowCity(citiesName)
          }}
        >
          {showCity.length === citiesName.length
            ? "La moyenne nationale"
            : "Toutes les villes"}
        </button>{" "}
        <br /> Utilisez la goutire sous le graphique pour cibler une priode
        dans les donnes.
      </p>
    </>
  )
}
